<template>
    <div :class="ReportListParameters == undefined ? 'right-content hr_jobpost w-100' : 'right-content'">
        <loader :is-visible="isLoading"></loader>

        <div class="col-md-12 p-0 float-left listing">
            <!-- <div class="expand-list">
                <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                    <em class="icon-solid arrow-solid-left text-white" style="font-size: 9pt;"></em>
                </a>
            </div> -->
            <div class="col-md-12 p-0 mb-2">
                <div class="theme-primary partition p-actions-expand">
                    <span class="p-name text-white">{{ reportName }}</span>
                    <span class="p-actions float-right mydbsize">
                        <a v-on:click="OpenFilter"
                            :class="isFilterExist > 0 ? 'p-action-btn text-white dash' : 'p-action-btn text-white dash'">
                            <em class="fa fa-filter"></em><br>{{ $t('Filter') }}
                        </a>
                        <a v-on:click="EditReport"
                            :class="CanEdit == true ? 'p-action-btn text-white dash' : 'p-action-btn text-white dash d-none'"
                            title="Edit Report">
                            <em class="fa fa-pencil text-white action-icon"></em><br>{{ $t('Edit') }}</a>
                        <a v-on:click="ExportToPDF('download')" class="p-action-btn text-white dash"
                            data-original-title="ExportToPDF">
                            <em class="fa fa-file-pdf-o"></em><br>{{ $t('PDF') }}</a>
                        <a v-if="ReportType != 'PIVOT'" v-on:click="ExportToPDF('print')"
                            class="p-action-btn text-white dash" data-original-title="Print">
                            <em class="fa fa-file-pdf-o"></em><br>{{ $t('PRINT') }}</a>
                        <a v-on:click="ExportToExcel" class="p-action-btn text-white dash"
                            data-original-title="ExportToExcel">
                            <em class="fa fa-file-excel-o"></em><br>{{ $t('Excel') }}</a>
                        <a v-show="showGraphBtn" v-on:click="OpenGraph" class="p-action-btn text-white dash">
                            <em class="fa fa-pie-chart"></em><br>{{ showgraph ? $t('CollapseGraph') : $t('ExpandGraph')
                            }}
                        </a>
                        <a v-on:click="OpenSortingPopup" class="p-action-btn text-white dash"
                            data-original-title="Sort">
                            <em class="fa fa-sort-amount-asc"></em><br>{{ $t('Sort') }}
                        </a>
                        <a v-on:click="OnBack" class="p-action-btn text-white dash" data-original-title="Back">
                            <em class="fa fa-angle-double-left"></em><br>{{ $t('Back') }}
                        </a>
                    </span>
                    <span class="collapse-head-btns">
                        <a href="javascript:;" class="collapse-btns">
                            <em class="fa fa-angle-up text-white"></em>
                        </a>
                    </span>
                </div>
                <div class="parentdiv datarepfix2">
                    <div class="verticle-action lft75 rt30">
                        <span class="action-list-box small" style="width:400px">
                            <span id="action-list1"
                                class="text-left p-0 datarep mt-3 w-90 float-left bg_outerdark borderbababa position-relative"
                                v-if="isSortPopup">
                                <div class="float-left col-12">
                                    <div class="container-fluid p-0">
                                    </div>
                                    <div class="row px-3 pt-3 pb-0" v-for="(input, index) in sortColummnarray">
                                        <div class="col-lg-5 pl-0 pr-3">
                                            <div class="form-group mb-0">
                                                <label class="pb-2">{{ $t('SortBy') }}</label>
                                                <select v-model="input.sortcolummnId" class="form-control"
                                                    :id="'SortValue' + index" v-if="reportListingInfo.TYPE == 'PIVOT'">
                                                    <option value="" selected disabled>Select Field</option>
                                                    <option
                                                        v-for="item in reportListingInfo.PIVOTROW.filter(x => x.Calculated_Field == false)"
                                                        :value="item.CustomFieldId != '' ? item.CustomFieldId : item.FieldName"
                                                        :key="item.CustomFieldId"
                                                        :disabled="(sortColummnarray.filter(x => x.sortcolummnId == item.CustomFieldId)).length > 0">
                                                        {{ item.DisplayName ? GetLocalizedValue(item.DisplayName) :
                                                            GetLocalizedValue(item.FieldName) }}</option>
                                                </select>
                                                <select v-model="input.sortcolummnId" class="form-control"
                                                    :id="'SortValue' + index" v-else>
                                                    <option value="" selected disabled>Select Field</option>
                                                    <option
                                                        v-for="item in reportListingInfo.FIELDS.filter(x => x.Calculated_Field == false)"
                                                        :value="item.CustomFieldId != '' ? item.CustomFieldId : item.FieldName"
                                                        :key="item.CustomFieldId"
                                                        :disabled="(sortColummnarray.filter(x => x.sortcolummnId == item.CustomFieldId)).length > 0">
                                                        {{ item.DisplayName ? GetLocalizedValue(item.DisplayName) :
                                                            GetLocalizedValue(item.FieldName) }}</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="col-lg-5 pl-0 pr-3">
                                            <div class="form-group mb-0">
                                                <label class="pb-2">{{ $t('OrderBy') }}</label>
                                                <select v-model="input.columnOrders" class="form-control">
                                                    <option value="" selected disabled>Select Order</option>
                                                    <option>{{ $t('ASC') }}</option>
                                                    <option>{{ $t('DESC') }}</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="col-lg-2 pl-0 pr-3 d-flex align-items-center">
                                            <div class="form-group mb-0">
                                                <a v-on:click="AddSortingForm(input)"><em
                                                        class="fa fa-plus-circle font-24 text-success mt-3"></em></a>
                                                <a v-show="sortColummnarray[0].sortcolummnId != ''"
                                                    v-on:click="RemoveSortingForm(input)"><em
                                                        class="fa fa-times-circle font-24 text-danger mt-3 ml-1"></em></a>
                                            </div>
                                        </div>
                                    </div>
                                    <a class="btn btn-success my-3 mr-2 pl-1" v-on:click="EnableSortingSearch(input)">
                                        <span class="fa fa-sort-amount-desc mr-2"></span>{{ $t('Sort') }}</a>
                                    <a class="btn btn-danger pl-1 mb-0" v-on:click="RemoveSortingFromList()"> <span
                                            class="fa fa-times mr-2"></span>{{ $t('ResetSort') }}</a>
                                </div>
                                <div class="va_lft25 top4"><em class="icon-solid arrow-solid-left text-secondary"
                                        style="font-size: 20pt;"></em>
                                </div>
                                <button type="button" class="close closebtnsmall" v-on:click="isSortPopup = false">
                                    <em aria-hidden="true" class="fa fa-times text"></em>
                                </button>
                            </span>
                        </span>
                    </div>
                </div>
            </div>
            <div class="col-md-12 p-0 mb-2">
                <div class="tab-pane mb-0">
                    <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                        <!-- Graph  -->
                        <div v-if="showgraph" class="card mb-3 probx">
                            <div class="card-header">
                                <h2>{{ $t('Graph') }}</h2>
                            </div>
                            <div class="card-body" id="Reportpdf">
                                <div class="row">
                                    <div class="col-md-12 col-lg-12 text-center">
                                        <div class="border bg-light position-absolute text-left p-0 d-none"
                                            style="z-index:99; height:auto; max-width:20%;">
                                            <ul class="graphinfo">
                                                <li><label>X-axis:</label>
                                                    <!-- {{ GetLocalizedValue(XAxisFieldName) }} -->
                                                    {{ XaxisGraphFieldName }}
                                                </li>
                                                <li><label>Y-axis:</label>
                                                    <!-- {{ GetLocalizedValue(YAxisFieldName) }} -->
                                                    {{ YaxisGraphFieldname }}
                                                </li>
                                                <li v-show="aggregateFunctionResult != 0">
                                                    <label># {{ AggregateFuncField }}:</label>{{ aggregateFunctionResult
                                                    }}
                                                </li>

                                            </ul>
                                        </div>
                                        <div class="vue-apexcharts"
                                            v-if="chartType == 'line' || chartType == 'bar' || chartType == 'horizontal_bar' || chartType == 'multiple_bar' || chartType == 'stacked_bar'">
                                            <VueApexCharts ref="chart" :type="chartType" height="400px" width="50%"
                                                :options="chartOptions" :series="chartSeries">
                                            </VueApexCharts>
                                        </div>
                                        <div class="vue-apexcharts" v-if="chartType == 'pie' || chartType == 'donut'">
                                            <VueApexCharts ref="chart" :type="chartType" height="auto" width="40%"
                                                :options="chartOptions" :series="chartSeries">
                                            </VueApexCharts>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div v-if="shownumbergraph" class="card mb-3 probx">
                            <div class="card-header">
                                <h2>{{ $t('Graph') }}</h2>
                            </div>
                            <div class="card-body" id="Reportpdf">
                                <div class="row">
                                    <div class="col-md-12 col-lg-12 text-center">
                                        <div class="border bg-light float-left text-left p-0  graphinfo_base_lft"
                                            style="z-index:99; height:auto; max-width:20%;">
                                            <ul class="graphinfo">
                                                <li><label>Y-axis:</label>
                                                    <!-- {{ GetLocalizedValue(YAxisFieldName) }} -->
                                                    {{ YaxisGraphFieldname }}
                                                </li>
                                                <li><label>Color-code:</label>{{ GetChartColorCode }}</li>
                                            </ul>
                                        </div>

                                        <span class="p-0 m-0 float-left graphinfo_base_rgt vue-apexcharts">
                                            <div class="col-sm-4 col-lg-4 px-0 bg-white mx-auto shadowder"
                                                style="min-height: 260px; padding-top: 100px;">
                                                <span class="ChartNumberClass">
                                                    <h1 :style="'color:' + GetChartColorCode">{{ GetChartColorData }}
                                                    </h1>
                                                </span>
                                            </div>
                                        </span>


                                        <!-- <div class="vue-apexcharts" style="height: 100px;">
                                            <h1 :style="'color:' + GetChartColorCode">{{ GetChartColorData }}</h1>
                                        </div> -->
                                    </div>
                                </div>
                            </div>
                        </div>
                        <!-- Graph  -->

                        <!-- Listing Table -->
                        <div class="tab-pane p-0 pb-2 border-0 active" role="tabpanel" id="tab-detail-content"
                            aria-labelledby="tab-detail-content">
                            <div class="listing">
                                <div class="table-responsive table-fix-header">
                                    <table class="table table-bordered mt-0" style="width:100%" v-if="noRecord"
                                        aria-label="NO Record Found">
                                        <thead>
                                            <th scope="col"></th>
                                        </thead>
                                        <tbody>
                                            <tr style="cursor:initial;">
                                                <td colspan="4">
                                                    <div
                                                        class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                        {{ $t("NoRecordfound") }}</div>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>

                                    <table class="table table-bordered mt-0" style="width:100%" id="Reportpdf"
                                        aria-label="View Record">

                                        <thead class="thead-bg">
                                            <tr>
                                                <th scope="col" v-for="(item, pindex) in previewgroupdata" :key="pindex"
                                                    v-if="showdata"
                                                    :class="{ 'text-right': item.ColumnFieldDataCode === 'decimal' || item.ColumnFieldDataCode === 'bigint' }">
                                                    {{ GetLocalizedValue((item.DisplayName != null && item.DisplayName
                                                        !=
                                                        '') ?
                                                        item.DisplayName : item.FieldName) }}
                                                </th>
                                                <th scope="col" v-for="(column, cindex) in previewcolumn" :key="cindex">
                                                    <span v-on:click="sortdata(column.FieldName, column.CustomFieldId)"
                                                        v-if="!column.Calculated_Field" v-bind:class="{
                                                            'sort': column.ColumnFieldDataCode !== null, 'tb_headerSortUp': SortExp == 'ASC' && SortBy == column.FieldName,
                                                            'tb_headerSortDown': SortExp == 'DESC' && SortBy == column.FieldName,
                                                            'justify-content-end': column.ColumnFieldDataCode === 'decimal' || column.ColumnFieldDataCode === 'bigint',
                                                            'justify-end-content': column.Calculated_Field === null
                                                        }">
                                                        {{ GetLocalizedValue((column.DisplayName != null &&
                                                            column.DisplayName
                                                            != '') ? column.DisplayName : column.FieldName) }}
                                                    </span>
                                                    <span v-else :class="{
                                                        'justify-end-content': column.ColumnFieldDataCode === 'decimal' || column.ColumnFieldDataCode === 'bigint'
                                                    }">
                                                        {{ GetLocalizedValue((column.DisplayName != null &&
                                                            column.DisplayName
                                                            != '') ? column.DisplayName : column.FieldName) }}
                                                    </span>
                                                </th>
                                            </tr>
                                        </thead>

                                        <tbody v-for="(item, previewindex) in previewdata" :key="previewindex">
                                            <tr v-for="(sitem, sindex) in previewdata[previewindex].data"
                                                v-if="showdata" :key="sindex">
                                                <template v-if="sindex == 0">
                                                    <td class="breakspaces" v-for="(gitem, gindex) in previewgroupdata"
                                                        :style="(gitem.ColumnFieldDataCode === 'bigint' || gitem.ColumnFieldDataCode === 'decimal') ? 'text-align: right;' : ''"
                                                        :key="gindex"
                                                        :class="{ 'onlyrtbod': true, '': item.totalRecord == null }"
                                                        :rowspan="item.totalRecord">
                                                        {{ (gitem.ColumnFieldDataCode == 'bool' ? item[gitem.FieldName]
                                                            == 1 || item[gitem.FieldName] == true ? 'Yes' : 'No' : '') ||
                                                            (item[gitem.FieldName] != null && item[gitem.FieldName] != '' ?
                                                                item[gitem.FieldName] : '') }}
                                                        ({{ item.totalRecord }})
                                                    </td>
                                                </template>
                                                <td v-for="(column, groupindex) in previewcolumn" :key="groupindex"
                                                    :style="(column.ColumnFieldDataCode === 'bigint' || column.ColumnFieldDataCode === 'decimal') ? 'text-align: right;' : ''">
                                                    <span
                                                        v-if="!(column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true)"
                                                        class="breakspaces" v-html="(column.ColumnFieldDataCode == 'bool' ? sitem[column.FieldName] == 1 || sitem[column.FieldName] == true ? 'Yes' : 'No' : '') ||

                                                            (sitem[column.FieldName] === 0 ? '0' :
                                                                (sitem[column.FieldName] != null && sitem[column.FieldName] != '' ?
                                                                    sitem[column.FieldName] : ''))">
                                                    </span>

                                                    <span
                                                        v-if="column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true"
                                                        class="overflow-visible pt-4"
                                                        style="width:242px;min-width:242px;">
                                                        <div class="progress">
                                                            <div class="progress-bar" role="progressbar"
                                                                :style="getProgressStyle(column, sitem)"
                                                                :aria-valuenow="(sitem[column.FieldName] / (column.TotalFieldValue || sitem[column.ParentTotalValue]) * 100).toFixed(0)"
                                                                aria-valuemin="0" aria-valuemax="100">
                                                                {{ (sitem[column.FieldName] / (column.TotalFieldValue ||
                                                                    sitem[column.ParentTotalValue]) *
                                                                    100).toFixed(0) }}%
                                                            </div>
                                                        </div>

                                                    </span>
                                                </td>
                                            </tr>
                                            <tr v-if="showcoldata">
                                                <td v-for="(column, columnindex) in previewcolumn" :key="columnindex"
                                                    :style="{
                                                        textAlign: (column.ColumnFieldDataCode === 'bigint' || column.ColumnFieldDataCode === 'decimal' || column.ColumnFieldDataCode == null) ? 'right' : '',
                                                        backgroundColor: column.FieldName === 'Overall Total' ? '#f0f0f0' : ''
                                                    }">
                                                    <span
                                                        v-if="!(column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true)"
                                                        class="breakspaces" v-html="column.ColumnFieldDataCode === 'bool'
                                                            ? (item[column.FieldName] == 1 || item[column.FieldName] === true ? 'Yes' : 'No')
                                                            : (item[column.FieldName] === 0
                                                                ? '0'
                                                                : (item[column.FieldName] != null && item[column.FieldName] !== ''
                                                                    ? (['decimal', 'bigint', null].includes(column.ColumnFieldDataCode)
                                                                        ? $n(item[column.FieldName], 'decimal2')
                                                                        : item[column.FieldName])
                                                                    : ''))">
                                                    </span>


                                                    <span
                                                        v-if="column.ColumnFieldDataCode == 'decimal' && column.IsLineGraph == true"
                                                        class="overflow-visible pt-4"
                                                        style="width:242px;min-width:242px;">
                                                        <div class="progress">
                                                            <div class="progress-bar" role="progressbar"
                                                                :style="getProgressStyle(column, item)"
                                                                :aria-valuenow="(item[column.FieldName] / (column.TotalFieldValue || item[column.ParentTotalValue]) * 100).toFixed(0)"
                                                                aria-valuemin="0" aria-valuemax="100">
                                                                {{ (item[column.FieldName] / (column.TotalFieldValue ||
                                                                    item[column.ParentTotalValue]) *
                                                                    100).toFixed(0) }}%
                                                            </div>
                                                        </div>

                                                    </span>

                                                </td>
                                            </tr>
                                        </tbody>

                                        <tbody v-for="(Dataitem, Dataindex) in TotalSumOfColumn" :key="Dataindex"
                                            :style="{ backgroundColor: '#f0f0f0' }">
                                            <tr v-if="showcoldata">
                                                <td v-for="(columnData, columnindex) in previewcolumn"
                                                    :key="columnindex"
                                                    :style="(columnData.ColumnFieldDataCode == null) ? 'text-align: right;' : ''">
                                                    <span class="breakspaces" v-html="(Dataitem[columnData.FieldName] !== null && Dataitem[columnData.FieldName] !== '' && Dataitem[columnData.FieldName] !== undefined)
                                                        ? $n(Dataitem[columnData.FieldName], 'decimal2')
                                                        : Dataitem[columnData.FieldName]">
                                                    </span>

                                                </td>
                                            </tr>
                                        </tbody>


                                    </table>
                                </div>
                                <Pager :class="{ 'd none': true, '': TotalRecords > 10 }"
                                    :numberofrecords="TotalRecords" @SetCurrentPage="SetCurrentPage" prev-text="Prev"
                                    first-text="First" last-text="Last" next-text="Next">
                                </Pager>
                            </div>
                        </div>
                        <!-- Listing Table -->
                    </div>
                    <!-- <div
                        :class="TotalRecords > 100000 ? 'table-responsive table-fix-header' : 'table-responsive table-fix-header d-none'">
                        <table class="table table-bordered mt-0" style="width:100%" aria-label="NO Record Found">
                            <thead>
                                <th scope="col"></th>
                            </thead>
                            <tbody>
                                <tr style="cursor:initial;">
                                    <td colspan="4">
                                        <div
                                            class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                            <a @click="DownloadExceedRecords"
                                                class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                {{ $t("LimitExceed") }}</a>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div> -->
                </div>
            </div>
        </div>
        <modalViewCustomReportFilter v-if="showViewCustomReportFilter" @close="showViewCustomReportFilter = false"
            :reportListingInfo="reportListingInfo">
        </modalViewCustomReportFilter>
    </div>
</template>
<script>
import ViewReportServices from "@/services/DataService";
import modalViewCustomReportFilter from "../../../components/Common/Modal/modalViewCustomReportFilter.vue"
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
import VueApexCharts from "vue-apexcharts";
import Pager from '../../../components/Common/Pager.vue';
// import * as pdfFonts from "pdfmake/build/vfs_fonts.js"; // <-- vfs_fonts has to be imported before pdfmake
//import pdfMake from 'pdfmake';
const pdfFonts = require("pdfmake/build/vfs_fonts");
const pdfMake = require("pdfmake/build/pdfmake");
import moment from "moment";

pdfMake.vfs = pdfFonts.pdfMake.vfs;


export default {

    components: {
        VueApexCharts,
        Pager,
        modalViewCustomReportFilter
    },
    data() {
        return {
            // filter
            showViewCustomReportFilter: false,
            isFilterExist: 0,
            isLoading: false,
            showdata: false,
            showcoldata: true,
            showgraph: false,
            reportName: '',
            showGraphBtn: true,
            viewreportparameters: '',
            Viewcount: 0,
            shownumbergraph: false,
            GetChartColorCode: '',
            GetChartColorData: '',

            //Sorting
            isSortPopup: false,
            sortColummnarray: [{ sortcolummnId: "", columnOrders: "" }],
            SortExp: null,
            SortBy: null,

            //Chart 
            chartTypes: null,
            chartType: "",
            chartOptions: {},
            chartSeries: [],
            chartOptionXAxisData: [],
            chartSeriesData: [],
            colourList: ['#8892d0', '#da9c49', '#94c5a3', '#e67d21', '#787878', '#c59e71', '#38b8df', '#f55e1a', '#207a92', '#13599c'],
            AggregateFuncField: null,
            aggregateFunctionResult: 0,
            XAxisFieldName: null,
            YAxisFieldName: null,
            groupGraphlist: [],
            GraphJson: [],

            //List
            previewcolumn: [],
            pivotData: [],
            previewgroupdata: [],
            reportListingInfo: [],
            previewdata: [],
            CanEdit: true,

            //pagination
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            TotalPages: 0,

            //Export to PDF
            allReocrdsData: null,
            TableBody: [],
            TableHeader: [],
            pdfpreviewdata: [],
            ExcelJsonData: null,
            count: 0,
            IsForPreview: 0,
            SingleSortArray: [],
            MainSortingArray: [],
            MainUpdatedArray: [],
            SelectedChartTypeId: '',
            noRecord: true,

            XaxisGraphFieldName: null,
            YaxisGraphFieldname: null,
            completionPercentage: 0,
            ExtraFieldName: null,
            ReportType: '',
            TotalSumOfColumn: [],
            pageType: ''
        }
    },

    props: {
        ReportListParameters: {
            type: Object
        }
    },
    created: async function () {
        debugger
        var vm = this;
        vm.isLoading = true;
        vm.viewreportparameters = vm.ReportListParameters == undefined ? JSON.parse(atob(vm.$route.params.ReportID)) : vm.ReportListParameters;
        await vm.GetChartTypes();
        await vm.GetPreviewData(vm.viewreportparameters.ReportID);
        if (vm.viewreportparameters.ACCESS == 1 || vm.viewreportparameters.IsSystemReport == true) {
            vm.CanEdit = false;
        }
        else {
            vm.CanEdit = true;
        }
        vm.Viewcount += 1;
    },
    methods: {
        getProgressStyle(column, item) {
            const value = item[column.FieldName] === 0 ? 0 :
                (item[column.FieldName] != null && item[column.FieldName] !== ''
                    ? item[column.FieldName] : '');

            const total = column.TotalFieldValue != null && column.TotalFieldValue !== '' && column.TotalFieldValue !== undefined
                ? column.TotalFieldValue
                : item[column.ParentTotalValue];

            const completionPercentage = (value && total) ? ((value / total) * 100).toFixed(0) : 0;

            return {
                width: `${completionPercentage}%`
            };
        },
        SetCurrentPage(e) {
            var vm = this;
            if (e.PagerMethod == 'a') {
                vm.pagerMethod(e.PageNumber);
            }
            else if (e.PagerMethod == 'b') {
                vm.SetPageSize(parseInt(e.PageSize));
            }

        },
        // open filter
        OpenFilter: function () {
            var vm = this;
            vm.showViewCustomReportFilter = true;
            $("#modalViewCustomReportFilter").modal("show");
            setTimeout(function () {
                $("#modalViewCustomReportFilter").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        RemoveNewFilterFromList() {

            var vm = this;
            vm.isLoading = true;
            vm.GetPreviewData(vm.viewreportparameters.ReportID);
            vm.MainUpdatedArray = [];
            vm.noRecord = false;
        },
        // show graph model based on ExpandGraph or CollapseGraph click
        OpenGraph() {
            var vm = this;

            if (vm.GetChartColorCode != '') {
                if (vm.shownumbergraph) {
                    vm.shownumbergraph = false;
                }
                else {
                    vm.shownumbergraph = true;
                }
            }
            else {
                if (vm.showgraph) {
                    vm.showgraph = false;
                }
                else {
                    vm.showgraph = true;
                }
            }
        },

        //  on Click back button 
        OnBack: function () {
            var vm = this;
            vm.$router.push({ name: 'CustomReports' })
        },

        // View/fetch  data on click of View button and based on its reportId
        GetPreviewData(ReportId) {
            debugger
            var vm = this;
            var params = `ReportId=${ReportId}&IsPaged=1`
            ViewReportServices.GetReportDetailsbyId(params).then(async (response) => {
                if (response.data == "No Data") {
                    vm.ShowAlert(vm.$t("ReportNotExist"), 'failure', true, vm.$t('Alert'));
                    vm.$router.push({ name: 'CustomReports' });
                    vm.isLoading = false;
                } else {
                    if (response.data != null && response.data != undefined && response.data != "") {
                        vm.reportListingInfo = response.data.JSON_To_Create_ReportListing[0].Info;
                        vm.isFilterExist = vm.reportListingInfo.FILTERS.length;
                        var listingData = response.data.ReportListing;
                        vm.reportName = vm.reportListingInfo.REPORTNAME;
                        vm.ReportType = vm.reportListingInfo.TYPE;
                        // vm.pivotData = 
                        vm.previewcolumn = [];
                        if (listingData != null && listingData != undefined && listingData == 1) {
                            if (vm.reportListingInfo.GRAPH.length > 0) {
                                await vm.SetGroupDataforGraph();
                            }
                            await vm.UpdatePreviewData();
                            await vm.GetSortingData();
                            vm.BindGraph();
                        }
                        else {
                            vm.noRecord = true;
                            vm.showgraph = false;
                            vm.showGraphBtn = false;
                            vm.isLoading = false;
                        }
                    } else {
                        vm.noRecord = true;
                        vm.showgraph = false;
                        vm.showGraphBtn = false;
                        vm.isLoading = false;
                    }
                }
            })
        },

        // Bind the Column List without grouping 
        Bindlisting: function (listingData) {
            debugger
            var vm = this;
            if (listingData != null && listingData != undefined) {

                if (vm.reportListingInfo.TYPE == 'PIVOT') {
                    for (let j in vm.reportListingInfo.PIVOTROW) {
                        const row = vm.reportListingInfo.PIVOTROW[j];

                        if (row.FieldName &&
                            row.DisplayName !== 'ROWNUMBER' &&
                            row.FieldName !== 'Total_Records' &&
                            row.DisplayName !== 'Total_Records') {

                            const newColumn = {
                                "FieldName": row.DisplayName != null && row.DisplayName !== '' ? row.DisplayName : row.FieldName,
                                "CustomFieldId": row.CustomFieldId,
                                "Calculated_Field": vm.reportListingInfo.FIELDS?.[j]?.Calculated_Field || false,
                                "ColumnFieldDataCode": row.ColumnFieldDataCode,
                                "IsLineGraph": row.IsLineGraph,
                                "TotalFieldValue": row.TotalFieldValue,
                                "ParentTotalValue": row.ParentTotalValue
                            };

                            console.log("Pushing to previewcolumn:", newColumn);

                            vm.previewcolumn.push(newColumn);
                        }
                    }
                    ;
                    vm.pivotData.forEach(field => {
                        vm.previewcolumn.push({
                            FieldName: field.value,
                            CustomFieldId: null,
                            Calculated_Field: null,
                            ColumnFieldDataCode: null,
                            IsLineGraph: null,
                            TotalFieldValue: null,
                            ParentTotalValue: null
                        });
                    });

                    vm.previewcolumn.push({
                        FieldName: "Overall Total",
                        CustomFieldId: null,
                        Calculated_Field: null,
                        ColumnFieldDataCode: null,
                        IsLineGraph: null,
                        TotalFieldValue: null,
                        ParentTotalValue: null
                    });
                }
                else {

                    for (let j in vm.reportListingInfo.FIELDS) {
                        if (vm.reportListingInfo.FIELDS[j].FieldName && vm.reportListingInfo.FIELDS[j].DisplayName != 'ROWNUMBER'
                            && vm.reportListingInfo.FIELDS[j].FieldName != 'Total_Records' && vm.reportListingInfo.FIELDS[j].DisplayName != 'Total_Records') {
                            vm.previewcolumn.push({
                                "FieldName": vm.reportListingInfo.FIELDS[j].DisplayName != null && vm.reportListingInfo.FIELDS[j].DisplayName != '' ? vm.reportListingInfo.FIELDS[j].DisplayName :
                                    vm.reportListingInfo.FIELDS[j].FieldName,
                                "CustomFieldId": vm.reportListingInfo.FIELDS[j].CustomFieldId, "Calculated_Field": vm.reportListingInfo.FIELDS[j].Calculated_Field,
                                "ColumnFieldDataCode": vm.reportListingInfo.FIELDS[j].ColumnFieldDataCode,
                                "IsLineGraph": vm.reportListingInfo.FIELDS[j].IsLineGraph,
                                "TotalFieldValue": vm.reportListingInfo.FIELDS[j].TotalFieldValue,
                                "ParentTotalValue": vm.reportListingInfo.FIELDS[j].ParentTotalValue
                            })
                        }
                    }
                }
                vm.noRecord = false;
            }
            else {
                vm.noRecord = true;
            }
        },

        // Bind the column list if the list is grouped.
        BindGroupListing: function (listingData) {
            debugger
            var vm = this;
            vm.previewgroupdata = [];
            vm.showdata = true;
            vm.showcoldata = false;
            var getindex = 0;
            if (listingData != null && listingData != undefined) {
                for (let i in vm.reportListingInfo.GROUP) {
                    for (let j in vm.reportListingInfo.FIELDS) {
                        if (vm.reportListingInfo.GROUP[i].FieldName != vm.reportListingInfo.FIELDS[j].FieldName) {
                            vm.previewcolumn.push({
                                "FieldName": vm.reportListingInfo.FIELDS[j].DisplayName != null && vm.reportListingInfo.FIELDS[j].DisplayName != '' ? vm.reportListingInfo.FIELDS[j].DisplayName :
                                    vm.reportListingInfo.FIELDS[j].FieldName,
                                "CustomFieldId": vm.reportListingInfo.FIELDS[j].CustomFieldId, "Calculated_Field": vm.reportListingInfo.FIELDS[j].Calculated_Field,
                                "ColumnFieldDataCode": vm.reportListingInfo.FIELDS[j].ColumnFieldDataCode,
                                "IsLineGraph": vm.reportListingInfo.FIELDS[j].IsLineGraph,
                                "TotalFieldValue": vm.reportListingInfo.FIELDS[j].TotalFieldValue,
                                "ParentTotalValue": vm.reportListingInfo.FIELDS[j].ParentTotalValue

                            })
                        }
                        if (vm.reportListingInfo.FIELDS[j].FieldName == vm.reportListingInfo.GROUP[0].FieldName) {
                            getindex = j;
                        }
                    }
                    vm.previewgroupdata.push({
                        "FieldName": vm.reportListingInfo.GROUP[i].DisplayName != null && vm.reportListingInfo.GROUP[i].DisplayName != '' ? vm.reportListingInfo.GROUP[i].DisplayName :
                            vm.reportListingInfo.GROUP[i].FieldName,
                        "CustomFieldId": vm.reportListingInfo.FIELDS[i].CustomFieldId, "ColumnFieldDataCode": vm.reportListingInfo.FIELDS[getindex].ColumnFieldDataCode
                    })
                }
                vm.noRecord = false;
            }
            else {
                vm.noRecord = true;
            }
        },

        // fetch the data for Graph
        async SetGroupDataforGraph(sortColummnarray, UpdatedFilterArray) {
            debugger
            var vm = this;
            vm.IsFilterValid();
            var temp = {
                "Info": {
                    "REPORTID": vm.reportListingInfo.REPORTID,
                    "REPORTNAME": vm.reportListingInfo.REPORTNAME,
                    "MODULENAME": vm.reportListingInfo.MODULENAME,
                    "SUBMODULECODE": vm.reportListingInfo.SUBMODULECODE,
                    "SUBMODULEID": vm.reportListingInfo.SUBMODULEID,
                    "PAGESIZE": parseInt(vm.PageSize),
                    "PAGENUMBER": parseInt(vm.PageNumber),
                    "IS_PAGED": 0,
                    "IsForPreview": vm.IsForPreview,
                    "FIELDS": vm.reportListingInfo.FIELDS,
                    "FILTERS": UpdatedFilterArray == undefined ? vm.reportListingInfo.FILTERS : UpdatedFilterArray,
                    "GROUP": [],
                    "CHARTGROUPARRAY": vm.reportListingInfo.CHARTGROUPARRAY,
                    "SORTING": vm.reportListingInfo.SORTING,
                    "GRAPH": vm.reportListingInfo.GRAPH,
                    "EXPRESSION": vm.reportListingInfo.EXPRESSION,
                    "EXPRESSIONFIELDSARRAY": vm.reportListingInfo.EXPRESSIONFIELDSARRAY,
                    "FromDate": vm.viewreportparameters.FromDate ? vm.viewreportparameters.FromDate : null,
                    "ToDate": vm.viewreportparameters.ToDate ? vm.viewreportparameters.ToDate : null
                }

            }
            var jsondata = JSON.stringify(temp);
            await ViewReportServices.PreviewReport(jsondata).then((response) => {
                if (response.data != null && response.data != undefined) {
                    vm.groupGraphlist = JSON.parse(response.data[0].Value);
                    if (UpdatedFilterArray != undefined) {
                        vm.BindGraph();
                    }
                }
            }).catch((err) => {
                console.log(err);
            })
        },

        // fetch the data when user change the paging 
        UpdatePreviewData(sortColummnarray, UpdatedFilterArray) {
            debugger
            var vm = this;
            vm.isLoading = true;
            var sortingArray = [];
            vm.pivotData = [];
            vm.IsFilterValid();
            vm.MainUpdatedArray = UpdatedFilterArray;
            for (var i in sortColummnarray) {
                if (sortColummnarray[i].sortcolummnId != undefined && sortColummnarray[i].sortcolummnId != "") {
                    sortingArray.push(
                        { "CustomFieldId": sortColummnarray[i].sortcolummnId, "SORTTYPE": sortColummnarray[i].columnOrders, "SortIndex": parseInt(i), "Calculated_Field": "" }
                    )
                    vm.reportListingInfo.FIELDS.forEach(arritem => {
                        var elem = sortingArray.find(x => x.CustomFieldId === arritem.CustomFieldId)
                        if (elem != undefined) {
                            elem.Calculated_Field = arritem.Calculated_Field;
                        }
                    })
                }
            }
            if (vm.SingleSortArray.length > 0 && sortingArray.length > 0) {
                const findsamesortvalue = sortingArray.findIndex(x => x.CustomFieldId === vm.SingleSortArray[0].sortcolummnId);
                if (findsamesortvalue != -1) {
                    sortingArray.splice(findsamesortvalue, 1);
                    if (vm.sortColummnarray.length > 1) {
                        vm.sortColummnarray.splice(findsamesortvalue, 1);
                    } else {
                        vm.sortColummnarray[findsamesortvalue].sortcolummnId = '';
                        vm.sortColummnarray[findsamesortvalue].columnOrders = '';
                    }
                }
            }
            vm.MainSortingArray = sortingArray;
            var temp = {
                "Info": {
                    "TYPE": vm.reportListingInfo.TYPE,
                    "REPORTID": vm.reportListingInfo.REPORTID,
                    "REPORTNAME": vm.reportListingInfo.REPORTNAME,
                    "MODULENAME": vm.reportListingInfo.MODULENAME,
                    "SUBMODULECODE": vm.reportListingInfo.SUBMODULECODE,
                    "SUBMODULEID": vm.reportListingInfo.SUBMODULEID,
                    "PAGESIZE": vm.PageSize,
                    "PAGENUMBER": parseInt(vm.PageNumber),
                    "IS_PAGED": 1,
                    "IsForPreview": vm.IsForPreview,
                    "FIELDS": vm.reportListingInfo.FIELDS,
                    "FILTERS": UpdatedFilterArray == undefined ? vm.reportListingInfo.FILTERS : UpdatedFilterArray,
                    "GROUP": vm.reportListingInfo.GROUP,
                    "SORTING": sortingArray == null || sortingArray == [] || sortingArray.length == 0 ? vm.reportListingInfo.SORTING : sortingArray,
                    "EXPRESSION": vm.reportListingInfo.EXPRESSION,
                    "EXPRESSIONFIELDSARRAY": vm.reportListingInfo.EXPRESSIONFIELDSARRAY,
                    "SINGLE_SORT": vm.SingleSortArray,
                    "FromDate": vm.viewreportparameters.FromDate ? vm.viewreportparameters.FromDate : null,
                    "ToDate": vm.viewreportparameters.ToDate ? vm.viewreportparameters.ToDate : null,
                    "PIVOTROW": vm.reportListingInfo.PIVOTROW ? vm.reportListingInfo.PIVOTROW : [],
                    "PIVOTCOLUMN": vm.reportListingInfo.PIVOTCOLUMN ? vm.reportListingInfo.PIVOTCOLUMN : [],
                    "PIVOTValues": vm.reportListingInfo.PIVOTValues ? vm.reportListingInfo.PIVOTValues : []

                }
            }
            var jsondata = JSON.stringify(temp);
            ViewReportServices.PreviewReport(jsondata).then(async (response) => {
                if (response.data != null && response.data != undefined) {
                    vm.previewcolumn = [];
                    var listingInfo = JSON.parse(response.data[0].Value);
                    if (listingInfo != null && listingInfo != undefined && listingInfo.ListingData != null) {
                        var MetaData = listingInfo.MetaData[0];
                        vm.previewdata = listingInfo.ListingData;
                        vm.TotalRecords = MetaData.TotalRecord;
                        vm.pivotData = listingInfo.PivotColumnValueData;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.TotalSumOfColumn = listingInfo.AggregateTotalsColumnWise;
                        if (MetaData.IsGrouping) {
                            vm.BindGroupListing(vm.previewdata);
                        }
                        else {
                            vm.Bindlisting(vm.previewdata);
                        }
                        vm.reportListingInfo.FIELDS.forEach(item => {
                            vm.previewdata.forEach(inneritem => {
                                var tempdata = inneritem[item.DisplayName] != null ? inneritem[item.DisplayName] : inneritem[item.FieldName];
                                if (item.ColumnFieldDataCode == 'date' && item.DateFormat == null && tempdata != undefined) {
                                    var defaultdatecheck = moment(tempdata, "MM/DD/YYYY", true);
                                    if (inneritem[item.DisplayName] != null && defaultdatecheck.isValid() == false) {
                                        inneritem[item.DisplayName] = vm.$options.filters.formatDate(inneritem[item.DisplayName])
                                    } else {
                                        inneritem[item.FieldName] = vm.$options.filters.formatDate(inneritem[item.FieldName])
                                    }
                                }
                            })
                        })
                    }
                    else {
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                        vm.showgraph = false;
                        vm.showGraphBtn = false;
                    }
                    vm.isLoading = false;
                }
                else {
                    vm.isLoading = false;
                }
            }).catch((err) => {
                console.log(err);
            })

        },

        // Bind the graph Values
        BindGraph: function () {
            debugger
            var vm = this;
            let graphData = vm.reportListingInfo.GRAPH[0]
            if (graphData == undefined) {
                vm.showGraphBtn = false;
                return false;
            }
            if (graphData.ChartColorCode == '') {
                if (graphData != null && graphData != undefined && graphData != '') {
                    vm.chartType = vm.chartTypes.find(x => x.id == graphData.chartTypeID).chart_Type.toLowerCase();
                    vm.SelectedChartTypeId = graphData.chartTypeID;
                    vm.AggregateFuncField = graphData.AggregateFunction;
                    vm.XAxisFieldName = vm.FilterColumnlist(graphData.XAxisField).CustomFieldName;
                    vm.YAxisFieldName = vm.FilterColumnlist(graphData.YAxisField).CustomFieldName;
                    vm.ExtraFieldName = vm.FilterColumnlist(graphData.ExtraFieldId).CustomFieldName;

                    if (vm.groupGraphlist.MetaData[0].IsGrouping) {
                        vm.SetGroupingGraphData(graphData.AggregateFunction);
                    }
                    (vm.chartType == "multiple_bar" || vm.chartType == "stacked_bar") ? '' : vm.SetGraphValues(vm.GraphJson, vm.XAxisFieldName, vm.YAxisFieldName);
                    //vm.CalculationAggregateFunctionResult(graphData.AggregateFunction);
                    if (vm.groupGraphlist.ListingData != null) {
                        vm.DrawGraph(graphData);
                        vm.showgraph = true;
                    }

                    vm.XaxisGraphFieldName = graphData.XAxisFieldName;
                    vm.YaxisGraphFieldname = graphData.YAxisFieldName;
                }
                else {
                    vm.showgraph = false;
                    vm.showGraphBtn = false;
                }
            } else {
                vm.shownumbergraph = true;
                vm.showgraph = false;
                vm.YAxisFieldName = vm.FilterColumnlist(graphData.YAxisField).CustomFieldName;
                vm.GetChartColorCode = graphData.ChartColorCode;
                vm.GetChartColorData = vm.groupGraphlist.ListingData[0].GData;

                vm.YaxisGraphFieldname = graphData.YAxisFieldName;


            }
        },
        SetGroupingGraphData(FuntionType) {
            debugger
            var vm = this;
            let ListingData = vm.groupGraphlist.ListingData;
            let setGraphArray = [];
            let XaxisField = vm.XAxisFieldName;
            let yAxisField = vm.YAxisFieldName;
            let ExtraFieldId = vm.ExtraFieldName;
            if (ListingData != null && ListingData != undefined) {
                if (vm.chartType == "multiple_bar" || vm.chartType == "stacked_bar") {
                    var multibarsData = [];
                    var keys = []
                    ListingData.forEach((mitem, mainindex) => {
                        var obj = {
                            XaxisField: mitem[XaxisField]
                        }
                        vm.chartOptionXAxisData.push(mitem[XaxisField])
                        mitem.Data.forEach(item => {
                            //var pushitem = item[yAxisField] != undefined ? item[yAxisField] : '-';

                            // var pushitem = vm.chartType == 'stacked_bar'
                            //     ? (item[ExtraFieldId] !== undefined ? item[ExtraFieldId] : '-')
                            //     : (item[yAxisField] !== undefined ? item[yAxisField] : '-');

                            var pushitem = item[ExtraFieldId] !== undefined ? item[ExtraFieldId] : '-';

                            var checkexist = keys.filter(x => x == pushitem);
                            obj[pushitem] = item.Countable;
                            if (checkexist.length == 0) {
                                keys.push(pushitem);
                            }
                        });
                        multibarsData.push(obj);
                    });
                    keys.forEach(function (x) {
                        var obj = {
                            name: x,
                            data: []
                        };
                        multibarsData.forEach(function (y) {
                            if (y[x]) {
                                obj.data.push(y[x]);
                            } else {
                                obj.data.push(0);
                            }
                        });
                        vm.chartSeriesData.push(obj);
                    });
                } else {
                    ListingData.forEach((item, mainindex) => {
                        if ((item[XaxisField] != undefined || XaxisField != undefined) && mainindex < 50) {
                            if (FuntionType == "Count" || FuntionType == "Sum" || FuntionType == "Avg" || FuntionType == "Min" || FuntionType == "Max") {
                                var pushitem = item[XaxisField] != undefined ? item[XaxisField] : item.data[0][XaxisField] + ` (${item.totalRecord})`;
                                for (var i = 0; i <= parseInt(item.totalRecord); i++) {
                                    if (i == 0) {
                                        setGraphArray.push({
                                            [XaxisField]: pushitem,
                                            [yAxisField]: item.GData
                                            // [yAxisField]: item.GData != null && item.GData != 0 && item.GData > item.totalRecord ? item.GData : item.totalRecord
                                        });
                                    }
                                }
                            }
                        }
                    })
                }
                vm.GraphJson = setGraphArray
                vm.showgraph = true;
            } else {
                vm.GraphJson = [];
                vm.XAxisFieldName = null;
                vm.YAxisFieldName = null;
                vm.showgraph = false;
                vm.showGraphBtn = false;
            }
        },
        // Draw graph 
        DrawGraph: function (graphData) {
            debugger
            var vm = this;
            var isHorizontal = false;
            var selectedchart = vm.chartTypes.find(x => x.id == vm.reportListingInfo.GRAPH[0].chartTypeID).chart_Type.toLowerCase();
            if (vm.chartType == "bar" || vm.chartType == "line" || vm.chartType == 'horizontal_bar' || vm.chartType == "multiple_bar") {
                if (vm.chartType == 'horizontal_bar') {
                    vm.chartType = "bar";
                    isHorizontal = true;
                } else if (vm.chartType == "multiple_bar") {
                    vm.chartType = "bar"
                }
                vm.chartSeries = selectedchart == "multiple_bar" ? vm.chartSeriesData : [{ name: vm.$t(vm.FilterColumnlist(graphData.YAxisField).CustomFieldName), data: vm.chartSeriesData }]
                vm.chartOptions = {
                    chart: {
                        toolbar: {
                            export: {
                                svg: {
                                    filename: vm.reportName,
                                },
                                png: {
                                    filename: vm.reportName,
                                },
                                csv: {
                                    headerCategory: vm.$t(vm.FilterColumnlist(graphData.XAxisField).CustomFieldName),
                                    filename: vm.reportName,
                                }
                            }
                        }
                    },
                    xaxis: {
                        categories: vm.chartOptionXAxisData,
                        title: {
