                        "SUBMODULEID": vm.reportListingInfo.SUBMODULEID,
                        "PAGESIZE": vm.PageSize,
                        "PAGENUMBER": parseInt(vm.PageNumber),
                        "IS_PAGED": 0,
                        "IsForPreview": vm.IsForPreview,
                        "FIELDS": vm.reportListingInfo.FIELDS,
                        "FILTERS": vm.MainUpdatedArray != undefined ? vm.MainUpdatedArray : vm.reportListingInfo.FILTERS,
                        "GROUP": vm.reportListingInfo.GROUP,
                        "SORTING": vm.MainSortingArray != undefined ? vm.MainSortingArray : vm.reportListingInfo.SORTING,
                        "EXPRESSION": vm.reportListingInfo.EXPRESSION,
                        "SINGLE_SORT": vm.SingleSortArray,
                        "EXPRESSIONFIELDSARRAY": vm.reportListingInfo.EXPRESSIONFIELDSARRAY,
                        "FromDate": vm.viewreportparameters.FromDate ? vm.viewreportparameters.FromDate : null,
                        "ToDate": vm.viewreportparameters.ToDate ? vm.viewreportparameters.ToDate : null,
                        "PIVOTROW": vm.reportListingInfo.PIVOTROW ? vm.reportListingInfo.PIVOTROW : [],
                        "PIVOTCOLUMN": vm.reportListingInfo.PIVOTCOLUMN ? vm.reportListingInfo.PIVOTCOLUMN : [],
                        "PIVOTValues": vm.reportListingInfo.PIVOTValues ? vm.reportListingInfo.PIVOTValues : []
                    }
                }
                var jsondata = JSON.stringify(temp);
                vm.ExcelJsonData = jsondata;
                if (exceldatavalue == undefined) {
                    await ViewReportServices.PreviewReport(jsondata).then(res => {
                        var pdflisting = JSON.parse(res.data[0].Value);
                        vm.pdfpreviewdata = pdflisting;
                        vm.count = vm.count + 1;
                    }).catch(err => {
                        console.log('Data not Found');
                    })
                }
