            await vm.$refs.chart?.chart.dataURI().then(uri => {
                URI = uri;
            });
            return URI;
        },

        //Export TO PDF functionality 
        ExportToPDF: async function (type) {
            debugger
            var vm = this;
            vm.isLoading = true;
            var GetLimitRecordsData = vm.GetTotalRecordsForExceed();
            if (vm.TotalRecords > GetLimitRecordsData) {
                vm.confirmR(vm.$t("LimitExceed"), true, (vm.$t("LimitExceed") + "  " + vm.$t("Report")), function () {
                    vm.GetPdfAndExcelApi();
                })
            } else {
                await vm.GetDataForPDFListing();
                if (vm.count != 0) {
                    var GraphData = {};
                    if (vm.chartOptions.legend != undefined) {
                        vm.chartOptions.legend.width = screen.availWidth - screen.availWidth * 74 / 100;
                    }
                    var graphURI = await vm.GetGraphImageURI();
                    var allRecordslisting = vm.pdfpreviewdata.ListingData;
                    let MetaData = vm.pdfpreviewdata.MetaData[0].IsGrouping;
                    let ReportName = vm.reportListingInfo.REPORTNAME.replace('.', '-');
                    vm.pageType = 'A4';

                    let pdfDetails;
                    let pageOrientation = '';
                    let GraphWidth = 300;

                    // Create PDF with  Grouping 
                    if (MetaData) {
                        await vm.BindPDFDataForGroup(allRecordslisting);

                    }
                    // Create PDF without  Grouping 
                    else {
                        await vm.BindPDFData(allRecordslisting);
                    }

                    //Set Page Type 
                    // if (vm.TableHeader.length > 5) {
                    //     pageType = "A1";
                    //     GraphWidth = 500
                    // }
                    // //Set Page  pageOrientation

                    // if (vm.TableHeader.length > 10) {
                    //     pageOrientation = 'landscape';
                    // }

                    if (vm.TableHeader.length > 8) {

                        vm.pageType = 'A4';

                        if (vm.ReportType == 'PIVOT' && type == 'download') {
                            vm.pageType = 'A0';
                        }


                        pageOrientation = vm.TableHeader.length > 6 ? 'landscape' : 'portrait';
                        GraphWidth = 600;
                    }
                    if (graphURI != null && graphURI != undefined) {
                        GraphData = {
                            image: graphURI.imgURI,
                            width: GraphWidth,
                            alignment: 'center'

                        }
                    }

                    //Set Page Type and pageOrientation
                    pdfDetails = {
                        pageSize: vm.pageType,
                        pageOrientation: pageOrientation,

                        info: {
                            title: ReportName,

                        },
                        content: [
                            GraphData,
                            { text: ` ${ReportName} `, style: 'subheader' },
                            {
                                style: 'table',
                                layout: {
                                    fillColor: function (rowIndex) {
                                        return (rowIndex === 0) ? '#CCCCCC' : null;
                                    },
                                    hLineWidth: function () { return 0.5; },
                                    vLineWidth: function () { return 0.5; },
                                },
                                table: {
                                    headerRows: 1,
                                    body: vm.TableBody,
                                    widths: vm.TableHeader.length > 10 ? Array(vm.TableHeader.length).fill('auto') : Array(vm.TableHeader.length).fill('*')
                                }
                            }
                        ],
                        styles: vm.SetPDFStyle(),
                        defaultStyle: {
                            //alignment: 'justify',
                            // size: 'legal',
                            // fontSize: 10,
                            // font: 'Roboto'
                            fontSize: 6, // Reduce font size
                            alignment: 'left'
                        }
                    }

                    if (type === "print") {
                        if (vm.TableHeader.length > 12) {
                            // Warn and fallback to open in new tab
                            pdfMake.createPdf(pdfDetails).open(); // open instead of direct print
                        } else {
                            pdfMake.createPdf(pdfDetails).print();
                        }
                    } else {
                        pdfMake.createPdf(pdfDetails).download(ReportName);
                    }
                    vm.isLoading = false;
                } else {
                    vm.ShowAlert(vm.$t("No_Record_Found"), 'danger', true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
            }
            vm.isLoading = false;
        },

        BindPDFData: function (allRecordslisting) {
            debugger
            var vm = this;
            vm.TableBody = [];
            vm.TableHeader = [];
            var TableHeader2 = [];
            //Add  Column for Table header
            var ColumnList = Object.keys(allRecordslisting[0])
            ColumnList.forEach((item) => {
                if (item != 'ROWNUMBER') {
                    vm.TableHeader.push({ text: vm.$t(item), style: 'tableHeader', alignment: 'center' })
                    TableHeader2.push({ text: item, style: 'tableHeader', alignment: 'center' })
                }
            })

            vm.TableBody.push(vm.TableHeader);

            allRecordslisting.forEach((item) => {
                let currentData = [];
                TableHeader2.forEach((heading) => {
                    currentData.push(item[heading.text]);
                })
                vm.TableBody.push(currentData);
            })

            if (vm.ReportType == 'PIVOT') {

                let totalsRow = [];
                let totalsData = vm.pdfpreviewdata.AggregateTotalsColumnWise?.[0] || {}; // safety fallback

                TableHeader2.forEach((heading) => {
                    const colKey = heading.text;
                    if (totalsData.hasOwnProperty(colKey)) {
                        totalsRow.push({ text: totalsData[colKey].toLocaleString(), bold: true, alignment: 'left' });
                    } else {
                        totalsRow.push({ text: '', bold: true });
                    }
                });

                // Add totals row with label if needed
                if (totalsRow.length > 0) {
                    // Optional: Replace first column with "Total" label
                    totalsRow[0] = { text: vm.$t("Overall Total"), bold: true, alignment: 'left' };
                    vm.TableBody.push(totalsRow);
                }
            }
        },

        BindPDFDataForGroup: function (allRecordslisting) {
            var vm = this;
            vm.TableBody = [];
            vm.TableHeader = [];
            var TableHeader2 = [];
            //Add Grouping Column for Table header
            vm.TableHeader.push({ text: vm.$t(Object.keys(allRecordslisting[0])[0]), style: 'tableHeader', alignment: 'center' })
            TableHeader2.push({ text: Object.keys(allRecordslisting[0])[0], style: 'tableHeader', alignment: 'center' })

            // Add all Column in Table Header 
            var columnList = Object.keys(allRecordslisting[0].data[0])
            columnList.forEach((item) => {
                vm.TableHeader.push({ text: vm.$t(item), style: 'tableHeader', alignment: 'center' })
                TableHeader2.push({ text: item, style: 'tableHeader', alignment: 'center' })
            })

            //Add First header array into Table Body
            vm.TableBody.push(vm.TableHeader);

            //Create Array for PDF
            allRecordslisting.forEach((item) => {
                item.data.forEach((heading, groupingindex) => {
                    let tableData = [];
                    TableHeader2.forEach((headerelement, headerindex) => {
                        if (groupingindex == 0 && headerindex == 0) {
                            var groupColumn = `${item[headerelement.text]} (${item.totalRecord})`
                            tableData.push({ rowSpan: parseInt(item.totalRecord), text: groupColumn })
                        }
                        else if (groupingindex == 1 && headerindex == 0) {
                            tableData.push("")
                        }
                        else {
                            if (heading[headerelement.text] != undefined && heading[headerelement.text] != "") {
                                tableData.push(heading[headerelement.text]);
                            }
                            else {
                                var tempitem = heading[headerelement.text] = "-";
                                tableData.push(tempitem);
                            }
                        }
                    })
                    vm.TableBody.push(tableData)
                })
            })
        },
        SetPDFStyle: function () {
            return {
                subheader: {
                    fontSize: 16,
                    bold: true,
                    margin: [0, 0, 0, 10]
                },
                table: {
                    margin: [0, 5, 0, 15]
                },

            };
        },
        ExportToExcel: async function () {
            debugger
            var vm = this;
            var GetLimitRecordsData = vm.GetTotalRecordsForExceed();
            if (vm.TotalRecords > GetLimitRecordsData) {
                vm.confirmR(vm.$t("LimitExceed"), true, (vm.$t("LimitExceed") + "  " + vm.$t("Report")), function () {
                    vm.GetPdfAndExcelApi();
                })
            } else {
                vm.isLoading = true;
                await vm.GetDataForPDFListing('Excel');
                if (vm.ExcelJsonData != null && vm.ExcelJsonData != undefined) {
                    var jsonData = vm.ExcelJsonData;
                    var params = `ReportId=${vm.reportListingInfo.REPORTID}&pagetype=preview&IsPaged=0`
                    ViewReportServices.GetExcelReport(params, jsonData).then((response) => {
                        var blob = new Blob([response.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;' });
                        var link = document.createElement('a');
                        link.href = window.URL.createObjectURL(blob);
                        const current = new Date();
                        const date = `${current.getDate()}_${current.getMonth() + 1}_${current.getFullYear()}`;
                        var fileName = `ReportData-${vm.reportListingInfo.REPORTNAME.replace('.', '-')}-${date}`;
                        fileName = fileName.split(" ").join("");
                        link.download = fileName;
                        link.click();
                        vm.isLoading = false;
                    }).catch((err) => { console.log(err); vm.isLoading = false; })
                } else {
                    vm.ShowAlert(vm.$t("No_Record_Found"), 'danger', true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
            }
        },
