        GetPdfAndExcelApi() {
            var vm = this;
            vm.isLoading = true;
            vm.IsFilterValid();
            var exceljson = {
                "Info": {
                    "REPORTID": vm.reportListingInfo.REPORTID,
                    "REPORTNAME": vm.reportListingInfo.REPORTNAME,
                    "MODULENAME": vm.reportListingInfo.MODULENAME,
                    "SUBMODULECODE": vm.reportListingInfo.SUBMODULECODE,
                    "SUBMODULEID": vm.reportListingInfo.SUBMODULEID,
                    "PAGESIZE": 10,
                    "PAGENUMBER": 1,
                    "IS_PAGED": 0,
                    "IsForPreview": vm.IsForPreview,
                    "FIELDS": vm.reportListingInfo.FIELDS,
                    "FILTERS": vm.MainUpdatedArray != undefined ? vm.MainUpdatedArray : vm.reportListingInfo.FILTERS,
                    "GROUP": vm.reportListingInfo.GROUP,
                    "SORTING": vm.MainSortingArray != undefined ? vm.MainSortingArray : vm.reportListingInfo.SORTING,
                    "EXPRESSION": vm.reportListingInfo.EXPRESSION,
                    "EXPRESSIONFIELDSARRAY": vm.reportListingInfo.EXPRESSIONFIELDSARRAY,
