            })
        },
        sortdata: function (filedName, fieldId) {
            var vm = this;
            var SortDir = "sort tb_headerSortDown";
            vm.SortBy = filedName;
            if (filedName === vm.SortBy) {
                if (vm.SortExp == "" || vm.SortExp == null) {
                    vm.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (vm.SortExp == "ASC") {
                    vm.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    vm.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            // var objSort = vm.sortColummnarray.filter(x=>x.sortcolummnId == fieldId)[0];
            // if(objSort !=undefined && objSort.columnOrders.length > 0){
            //     objSort.columnOrders=vm.SortExp;
            // }
            // else{
            //     vm.sortColummnarray.push({'sortcolummnId':fieldId,'columnOrders':vm.SortExp})
            // }
            // vm.UpdatePreviewData(vm.sortColummnarray);
            vm.SingleSortArray = [];
            vm.SingleSortArray.push({ 'sortcolummnId': fieldId, 'columnOrders': vm.SortExp })
            vm.UpdatePreviewData();
            $("#" + filedName + '  span:first-child').attr("class", SortDir);
        },
        IsFilterValid: function () {
            var vm = this;
            vm.reportListingInfo.FILTERS.forEach(fitem => {
                if (fitem.isManuallyAdded) {
                    if (fitem.operator_display_name == 'Is Empty' || fitem.operator_display_name == 'Is Not Empty' || fitem.operator_display_name == 'Is Off Day' || fitem.operator_display_name == 'Is Week End') {
                        console.log('Valid Filter')
                    } else if (fitem.FieldName != '' && fitem.Value1 != '' && fitem.OPERATOR_ID != '') {
                        console.log('Valid Filter')
                    } else {
                        let FilterNotValid = vm.reportListingInfo.FILTERS.findIndex(x => x.CustomFieldId === fitem.CustomFieldId);
                        vm.reportListingInfo.FILTERS.splice(FilterNotValid, 1);
                    }
                }
            })
        }
    }
}
