<script>
import FilterViewReportServices from "@/services/DataService";
import { ValidationObserver, Form, ValidationProvider as Field, extend, ErrorMessage } from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
export default {
    setup() {
        Object.keys(rules).forEach(rule => {
            extend(rule, rules[rule]);
        });
    },
    components: {
        Form, Field, ValidationObserver
    },
    data() {
        return {
            isLoading: false,
            isFilterExist: 0,
            selectedOPValue: '',
            GetOperator: [],
            options: [],
            isBinding: false,
            ArrayforFilterpopup: [],
            FilterClose: false,
            FilterCategory: ["AND", "OR"],
            modelfiltertype: 'AND',
            multiselectArray: [],
            daterangeArray: [],
            MainComponentkey: 0
        }
    },
    props: {
        reportListingInfo: {
            type: Object
        }
    },
    created() {
        var vm = this;
        vm.GetDropDownValueOnFilter();
    },
    watch: {
        FilterClose(newval) {
            if (newval) {
                this.GetDropDownValueOnFilter();
            }
        }
    },
    mounted: function () {
        $('#modalViewCustomReportFilter').modal({
            backdrop: 'static',
            keyboard: false
        })
    },
    methods: {
        removeElement() {
            this.$forceUpdate();
        },
        selectionChange() {
            this.$forceUpdate();
        },
        async close() {
            var vm = this;
            $("#modalViewCustomReportFilter").modal("hide"); $("body").removeClass("modal-open");
            $(".modal-backdrop").remove();
            await this.ApplyNewFilterToList('Close');
            this.$emit("close");
            //vm.FilterClose = true;
        },
        //   Get Filter Select DDL 
        GetFilterSelectDDL(filterdata) {
            debugger
            var vm = this;
            filterdata.selectOptions = [];
            vm.multiselectArray = [];
            var Getddldata = [];
            vm.isLoading = true;
            FilterViewReportServices.GetFilterDDLselectvalue(filterdata.CustomFieldId).then(response => {
                if (response.data.length > 0) {
                    Getddldata = response.data;
                    Getddldata.forEach(ddlitem => {
                        filterdata.selectOptions.push({
                            value: ddlitem.value,
                            label: ddlitem.name
                        })
                        vm.multiselectArray.push({
                            value: ddlitem.value,
                            label: ddlitem.name
                        })
                    })
                    vm.isLoading = false;
                } else { vm.isLoading = false; }
            }).catch(err => {
                console.log(err)
            })
        },
        // Get oprators Data For Filter Apply on View Report 
        GetOperatordata(filterdata) {
            debugger
            var vm = this;
            var CustomFieldId = filterdata.CustomFieldId;
            var params = `dataType=${filterdata.ColumnFieldDataCode}&customField=${CustomFieldId}`;
            FilterViewReportServices.GetDataOperator(params).then(response => {
                filterdata.OperatorList = response.data.DATA;
                if (filterdata.OperatorExpression) {
                    let operaterData = filterdata.OperatorList.find(x => x.OPERATOR_ID === parseInt(filterdata.OPERATOR_ID));
                    filterdata.OperatorExpression = operaterData.OPERATOR_EXPRESSION;
                    vm.multiselectArray = filterdata.selectOptions;
                }
                vm.MainComponentkey += 1;
            })
        },
        // Get Filter DDL for Date Range
        GetDateRangeFilterDDL(filterdata) {

            var vm = this;
            filterdata.selectOptions = [];
            vm.daterangeArray = [];
            var Getddldata = [];
            vm.isLoading = true;
            FilterViewReportServices.GetDateRangeFilterDDL().then(response => {
                setTimeout(function () {
                    Getddldata = JSON.parse(response.data[0].Value).Data;
                    Getddldata.forEach(ddlitem => {
                        filterdata.selectOptions.push({
                            value: ddlitem.date_range_name,
                            label: ddlitem.date_range_value
                        })
                        vm.daterangeArray.push({
                            value: ddlitem.date_range_name,
                            label: ddlitem.date_range_value
                        })
                    })
                    vm.isLoading = false;
                }, 2000);
            })
        },
        async ApplyNewFilterToList(closevalue) {
            var vm = this;
            this.reportListingInfo.FILTERS.forEach(function (x) {
                let operaterData = x.OperatorList.find(y => y.OPERATOR_ID == x.OPERATOR_ID);
                let firstValue = '\'' + x.Value1 + '\'';
                let secondValue = '\'' + x.Value2 + '\'';
                if (x.ColumnFieldDataCode == 'select' && (operaterData.OPERATOR_EXPRESSION == 'IN' || operaterData.OPERATOR_EXPRESSION == 'NOT IN')) {
                    let valArray = [];
                    x.Value1.forEach(i => {
                        valArray.push(i.value);
                    })
                    firstValue = '\'' + valArray.join('\',\'') + '\''
                }
                if (x.ColumnFieldDataCode == 'decimal' || x.ColumnFieldDataCode == 'int' || x.ColumnFieldDataCode == 'bigint') {
                    x.Value1 = x.Value1;
                    x.Value2 = x.Value2;
                    firstValue = x.Value1;
                    secondValue = x.Value2;
                }
                x.OperatorExpression = vm.ConvertOperatorToExpression(operaterData.OPERATOR_EXPRESSION, firstValue, secondValue);
                x.FilterTypeAndorOr = vm.modelfiltertype;
            });
            if (closevalue == undefined) {
                vm.isLoading = true;
                if (vm.reportListingInfo.GRAPH.length > 0) {
                    await vm.$parent.SetGroupDataforGraph([], vm.reportListingInfo.FILTERS);
                }
                vm.$parent.UpdatePreviewData([], vm.reportListingInfo.FILTERS);
                vm.isLoading = false;
                vm.close();
            }
        },
        RemoveNewFilterFromList() {
            var vm = this;
            vm.$parent.RemoveNewFilterFromList();
            vm.close();
        },
        PreventValues(event) {
            var vm = this;
            var ekey = event.keyCode;
            if (ekey == 69 || ekey == 107 || ekey == 109) { event.preventDefault(); }
        },
        GetDropDownValueOnFilter() {

            var vm = this;
            console.log(vm.reportListingInfo.FILTERS);
            vm.reportListingInfo.FILTERS.forEach(i => {
                vm.modelfiltertype = i.FilterTypeAndorOr;
                vm.GetOperatordata(i);
                if (typeof i.isManuallyAdded === 'undefined') {
                    i.isManuallyAdded = false;
                }
                if (i.ColumnFieldDataCode == 'select' && i.PickListOption == 'Lookup') {
                    var GetMultiddlvalue = [];
                    vm.GetFilterSelectDDL(i);
                    setTimeout(function () {
                        if (i.OperatorExpression == 'IN' || i.OperatorExpression == 'NOT IN') {
                            if (typeof i.Value1 != 'object') {
                                var multiddl = i?.Value1.replace(/"|'/g, '');
                                GetMultiddlvalue = multiddl?.split(',');
                                i.Value1 = [];
                                GetMultiddlvalue.forEach(ddlvalues => {
                                    const isddlvalueexist = i.selectOptions.find(j => j.value === ddlvalues)
                                    if (isddlvalueexist != undefined) {
                                        var multiselectdata = { value: isddlvalueexist.value, label: isddlvalueexist.label }
                                        i.Value1.push(multiselectdata);
                                    }
                                })
                            }
                        }
                    }, 2000);
                }
                else if (i.ColumnFieldDataCode == 'select' && i.PickListOption != 'Lookup') {

                    i.selectOptions = i.selectOptions || [];
                    vm.multiselectArray = vm.multiselectArray || [];

                    // Split comma-separated picklist values (trim to remove extra spaces)
                    if (i.PickListOption && i.PickListOption.trim() !== '') {
                        const options = i.PickListOption.split(',').map(opt => opt.trim());

                        options.forEach(opt => {
                            const optionObj = {
                                value: opt,
                                label: opt
                            };

                            i.selectOptions.push(optionObj);
                            vm.multiselectArray.push(optionObj);
                        });
                    }

                    setTimeout(function () {
                        if (i.OperatorExpression == 'IN' || i.OperatorExpression == 'NOT IN') {
                            if (typeof i.Value1 != 'object') {
                                var multiddl = i?.Value1.replace(/"|'/g, '');
                                GetMultiddlvalue = multiddl?.split(',');
                                i.Value1 = [];
                                GetMultiddlvalue.forEach(ddlvalues => {
                                    const isddlvalueexist = i.selectOptions.find(j => j.value === ddlvalues)
                                    if (isddlvalueexist != undefined) {
                                        var multiselectdata = { value: isddlvalueexist.value, label: isddlvalueexist.label }
                                        i.Value1.push(multiselectdata);
                                    }
                                })
                            }
                        }
                    }, 2000);
                }
                else if ((i.ColumnFieldDataCode == 'datetime' || i.ColumnFieldDataCode == 'date') && i.operator_display_name == 'Date Range') {
                    // setTimeout(function () {
                    // vm.GetDateRangeFilterDDL(i);},2000);
                    vm.GetDateRangeFilterDDL(i);
                }
            })
        },
        AddFilterRow: function () {

            var vm = this;
            this.$refs.FilterForm.validate().then(function (valid) {
                if (valid) {
                    vm.reportListingInfo.FILTERS.push({
                        "isManuallyAdded": true,
                        "CustomFieldId": "",
                        "FieldName": "",
                        "ActualDataType": "string",
                        "OperatorExpression": "",
                        "FilterTypeAndorOr": "AND",
                        "OPERATOR_ID": "",
                        "operator_display_name": "",
                        "Value1": "",
                        "Value2": "",
                        "IsMandatoryforFilter": false,
                        "FieldIcons": "",
                        "DisplayName": "",
                        "PickListOption": null,
                        "ColumnFieldDataCode": "",
                        "selectedOPValue": "",
                        "selectOptions": []
                    });
                }
            });
        },
        OnChangeFieldDdl: function (filterData) {
            debugger
            filterData.OperatorExpression = "";
            filterData.OPERATOR_ID = "";
            filterData.selectOptions = [];
            if (this.reportListingInfo.TYPE == 'NORMAL') {
                this.reportListingInfo.FIELDS.forEach(function (x) {
                    if (x.CustomFieldId == filterData.CustomFieldId) {
                        filterData.DisplayName = x.DisplayName;
                        filterData.ActualDataType = x.ColumnFieldDataCode;
                        filterData.ColumnFieldDataCode = x.ColumnFieldDataCode;
                        filterData.FieldName = x.FieldName;
                        filterData.FieldIcons = x.FieldIcons;
                        filterData.FieldIcons = x.FieldIcons;
                    }
                });
            }
            else {
                this.reportListingInfo.PIVOTROW.forEach(function (x) {
                    if (x.CustomFieldId == filterData.CustomFieldId) {
                        filterData.DisplayName = x.DisplayName;
                        filterData.ActualDataType = x.ColumnFieldDataCode;
                        filterData.ColumnFieldDataCode = x.ColumnFieldDataCode;
                        filterData.FieldName = x.FieldName;
                        filterData.FieldIcons = x.FieldIcons;
                        filterData.FieldIcons = x.FieldIcons;
                    }
                });
            }

            this.GetOperatordata(filterData);
        },
        OnChangeOperator: function (filterData) {
            debugger
            var vm = this;
            filterData.selectOptions = [];
            vm.multiselectArray = [];
            var Getddldata = [];
            vm.isLoading = true;
            if (filterData.OPERATOR_ID > 0) {
                let operaterData = filterData.OperatorList.find(x => x.OPERATOR_ID === parseInt(filterData.OPERATOR_ID));
                filterData.OperatorExpression = operaterData.OPERATOR_EXPRESSION;
                filterData.operator_display_name = operaterData.OPERATOR_DISPLAY_NAME;
                if (filterData.OperatorExpression == "IN" || filterData.OperatorExpression == "NOT IN") {
                    filterData.Value1 = [];
                } else {
                    filterData.Value1 = "";
                }
            }
            if (filterData.ColumnFieldDataCode == 'select') {
                FilterViewReportServices.GetFilterDDLselectvalue(filterData.CustomFieldId).then(response => {
                    if (response.data.length > 0) {
                        Getddldata = response.data;
                        Getddldata.forEach(ddlitem => {
                            filterData.selectOptions.push({
                                value: ddlitem.value,
                                label: ddlitem.name
                            });
                            vm.multiselectArray.push({
                                value: ddlitem.value,
                                label: ddlitem.name
                            });
                        })
                        vm.isLoading = false;
                    } else { vm.isLoading = false; }
                }).catch(err => {
                    console.log(err)
                })
            }
            if ((filterData.ActualDataType == 'datetime' || filterData.ActualDataType == 'date') && filterData.OperatorExpression == 'DateRange') {
                vm.GetDateRangeFilterDDL(filterData);
            }
            vm.isLoading = false;
        },
        DeleteFilterRow: function (index) {
            let vm = this;
            this.reportListingInfo.FILTERS.splice(index, 1);
        },
        TruncateStr: function (str, len) {
            var convertstring = '' + str;
            if (convertstring != null)
                if (convertstring.length <= len) return str;
                else return convertstring.substring(0, len) + "...";
        }
    }
}
</script>
<template>
    <div id="modalViewCustomReportFilter" role="dialog" class="modal my-popups centre fade" v-cloak>
        <div role="document" class="modal-dialog">
            <div class="modal-content">
                <ValidationObserver ref="FilterForm" v-slot="{ handleSubmit }">
                    <form @submit.prevent="handleSubmit(ApplyNewFilterToList)" ref="FilterForm1">
                        <div class="modal-header mheader-user">
                            <button type="button" class="close" v-on:click="close()">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                            <h4 id="myModalLabel" class="modal-title">{{ $t('FILTER') }}</h4>
                        </div>
                        <div class="modal-body">
                            <loader :is-visible="isLoading"></loader>
                            <div class="list-item row filter-div">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                    <div class="form-content">
                                        <div id="divshowSearchCriteriaList"
                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                            <div class="heading-border">
                                                <h5 class="h5">
                                                    <span> Filter</span><small class="float-right">
                                                        <a href="javascript:;" style="cursor: pointer;" title="Add"
                                                            v-on:click="AddFilterRow()"><em
                                                                class="fa fa-plus pr-2"></em> Add </a></small>
                                                </h5>
                                            </div>
                                            <div class="listing mt-2">
                                                <div class="mb-2">
                                                    <label>Include data if it matches</label>
                                                    <select class="form-control col-4" v-model="modelfiltertype">
                                                        <option v-for="filtertype in FilterCategory"
                                                            :value="filtertype">{{ filtertype }}</option>
                                                    </select>
                                                </div>
                                                <div class="table-responsive overflow-visible">
                                                    <table aria-describedby="serachfilter"
                                                        class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline">
                                                        <thead class="thead-bg">
                                                            <tr>
                                                                <th scope="col" style="min-width: 180px;">
                                                                    <span>Name</span>
                                                                </th>
                                                                <th scope="col" style="min-width: 180px;">
                                                                    <span>Operator</span>
                                                                </th>
                                                                <th scope="col" style="min-width: 480px;">
                                                                    <span>Value</span>
                                                                </th>
                                                                <th scope="col" style="min-width: 50px;">
                                                                    <span>Action</span>
                                                                </th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-for="(filterdata, index) in reportListingInfo.FILTERS"
                                                                v-if="reportListingInfo.FILTERS.length > 0">

                                                                <td v-if="reportListingInfo.TYPE != 'PIVOT'">
                                                                    <span class="form-control disabled"
                                                                        v-if="!filterdata.isManuallyAdded">{{
                                                                            GetLocalizedValue(filterdata.FieldName,
                                                                                filterdata.DisplayName) }}</span>
                                                                    <Field :name="'Field' + index"
                                                                        v-slot="{ field, errors, errorMessage, meta }"
                                                                        rules="required"
                                                                        v-if="filterdata.isManuallyAdded">
                                                                        <select v-bind="field"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            v-on:change="OnChangeFieldDdl(filterdata)"
                                                                            v-model="filterdata.CustomFieldId">
                                                                            <option value="">Select field</option>
                                                                            <option :value="item.CustomFieldId"
                                                                                v-for="item in reportListingInfo.FIELDS"
                                                                                :disabled="(reportListingInfo.FILTERS.filter(x => x.CustomFieldId == item.CustomFieldId)).length > 0"
                                                                                v-if="item.Calculated_Field === false">
                                                                                {{ GetLocalizedValue(item.FieldName,
                                                                                    item.DisplayName) }}</option>
                                                                        </select>
                                                                    </Field>
                                                                </td>
                                                                <td v-else>
                                                                    <span class="form-control disabled"
                                                                        v-if="!filterdata.isManuallyAdded">{{
                                                                            GetLocalizedValue(filterdata.FieldName,
                                                                                filterdata.DisplayName) }}</span>
                                                                    <Field :name="'Field' + index"
                                                                        v-slot="{ field, errors, errorMessage, meta }"
                                                                        rules="required"
                                                                        v-if="filterdata.isManuallyAdded">
                                                                        <select v-bind="field"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            v-on:change="OnChangeFieldDdl(filterdata)"
                                                                            v-model="filterdata.CustomFieldId">
                                                                            <option value="">Select field</option>
                                                                            <option :value="item.CustomFieldId"
                                                                                v-for="item in reportListingInfo.PIVOTROW"
                                                                                :disabled="(reportListingInfo.FILTERS.filter(x => x.CustomFieldId == item.CustomFieldId)).length > 0"
                                                                                v-if="item.Calculated_Field === false">
                                                                                {{ GetLocalizedValue(item.FieldName,
                                                                                    item.DisplayName) }}</option>
                                                                        </select>
                                                                    </Field>
                                                                </td>
                                                                <td style="overflow:visible!important;">
                                                                    <!-- {{ filterdata.operator_display_name }} -->
                                                                    <Field :name="'Operator' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required"
                                                                        style="overflow:visible!important; width:100%!important;">
                                                                        <select v-bind="field"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            v-model="filterdata.OPERATOR_ID"
                                                                            v-on:change="OnChangeOperator(filterdata)"
                                                                            :key="MainComponentkey">
                                                                            <option value="">Select Operator</option>
                                                                            <option
                                                                                v-for="item in filterdata.OperatorList"
                                                                                :value="item.OPERATOR_ID">{{
                                                                                    item.OPERATOR_DISPLAY_NAME }}</option>
                                                                        </select>
                                                                    </Field>
                                                                </td>
                                                                <td
                                                                    :style="((filterdata.ActualDataType == 'select' && (filterdata.OperatorExpression == 'IN' || filterdata.OperatorExpression == 'NOT IN') ? 'display: contents;' : ''))">
                                                                    <span
                                                                        v-if="filterdata.OperatorExpression == 'Is Not Null' || filterdata.OperatorExpression == 'Is Null'">--</span>

                                                                    <Field :name="'StringFirstValue' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required"
                                                                        v-if="(filterdata.ActualDataType == 'string' || filterdata.ActualDataType == 'bool') && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <input type="text" id="inpttxt"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            v-model="filterdata.Value1"
                                                                            :style="(filterdata.OperatorExpression == 'between' ? 'width: 45%;float: left;' : '')" />
                                                                    </Field>

                                                                    <Field :name="'StringSecondValue' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required"
                                                                        v-if="filterdata.ActualDataType == 'string' && filterdata.OperatorExpression == 'between'">
                                                                        <input type="text" id="inpttxt2"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            v-model="filterdata.Value2"
                                                                            style="width: 45%;float: left;margin-left: 50px;" />
                                                                    </Field>

                                                                    <Field :name="'NumberFirstValue' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required"
                                                                        v-if="(filterdata.ActualDataType == 'decimal' || filterdata.ActualDataType == 'int' || filterdata.ActualDataType == 'bigint') && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <input type="number" id="inpttxtbtw"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            :style="(filterdata.OperatorExpression == 'between' ? 'width: 45%;float: left;' : '')"
                                                                            @keydown="PreventValues($event)"
                                                                            v-model="filterdata.Value1" />
                                                                    </Field>

                                                                    <Field :name="'NumberSecondValue' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required"
                                                                        v-if="(filterdata.ActualDataType == 'decimal' || filterdata.ActualDataType == 'int' || filterdata.ActualDataType == 'bigint') && filterdata.OperatorExpression == 'between'">
                                                                        <input type="number" id="inpttxtbtw"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            style="width: 45%;float: left;margin-left: 50px;"
                                                                            @keydown="PreventValues($event)"
                                                                            v-model="filterdata.Value2" />
                                                                    </Field>

                                                                    <Field :name="'DateFirstValue' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required"
                                                                        v-if="(filterdata.ActualDataType == 'date' || filterdata.ActualDataType == 'datetime') && filterdata.OperatorExpression != 'DateRange' && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <input type="date" max="9999-12-31"
                                                                            id="inpttxtdate1"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            :style="(filterdata.OperatorExpression == 'between' ? 'width: 45%;float: left;' : '')"
                                                                            v-model="filterdata.Value1" />
                                                                    </Field>

                                                                    <Field :name="'DateSecondValue' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required"
                                                                        v-if="(filterdata.ActualDataType == 'date' || filterdata.ActualDataType == 'datetime') && filterdata.OperatorExpression != 'DateRange' && filterdata.OperatorExpression == 'between'">
                                                                        <input type="date" max="9999-12-31"
                                                                            :min="filterdata.Value1"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            style="width: 45%;float: left;margin-left: 20px;"
                                                                            v-model="filterdata.Value2" />
                                                                    </Field>

                                                                    <div
                                                                        v-if="filterdata.ActualDataType == 'select' && filterdata.OperatorExpression != 'IN' && filterdata.OperatorExpression != 'NOT IN' && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <Field :name="'Selectddl' + index"
                                                                            v-slot="{ field, errors, errorMessage }"
                                                                            rules="required">
                                                                            <select v-bind="field"
                                                                                :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                                v-model="filterdata.Value1">
                                                                                <option value="">Select value</option>
                                                                                <option
                                                                                    v-for="DDLitem in filterdata.selectOptions"
                                                                                    :key="DDLitem.value"
                                                                                    :value="DDLitem.value">
                                                                                    {{ truncateText(DDLitem.label, 70)
                                                                                    }}
                                                                                </option>
                                                                            </select>
                                                                        </Field>
                                                                    </div>

                                                                    <div
                                                                        v-if="(filterdata.ActualDataType == 'datetime' || filterdata.ActualDataType == 'date') && filterdata.OperatorExpression == 'DateRange'">
                                                                        <Field :name="'DateRangeValue' + index"
                                                                            v-slot="{ field, errors, errorMessage }"
                                                                            rules="required">
                                                                            <select
                                                                                :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                                v-model="filterdata.Value1">
                                                                                <option value="">Select value</option>
                                                                                <option
                                                                                    v-for="DDLitem in daterangeArray"
                                                                                    :key="DDLitem.value"
                                                                                    :value="DDLitem.value"
                                                                                    :title="DDLitem.label">{{
                                                                                        DDLitem.label }}</option>
                                                                            </select>
                                                                        </Field>
                                                                    </div>

                                                                    <div style="padding: 0.375rem 0.5rem;"
                                                                        v-if="filterdata.ActualDataType == 'select' && (filterdata.OperatorExpression == 'IN' || filterdata.OperatorExpression == 'NOT IN')">
                                                                        <Field :name="'MultiSelectddl' + index"
                                                                            v-slot="{ field, errors, errorMessage }"
                                                                            rules="required">
                                                                            <Multiselect
                                                                                style="box-sizing:border-box; margin-top: 0px; padding-left: 0px; padding-right:0px;"
                                                                                v-bind="field"
                                                                                v-model="filterdata.Value1"
